import { NextResponse } from 'next/server'
import { syncInboxFromIMAP } from '@/lib/imapIngestion'

/**
 * POST /api/sync-inbox
 * Fetches emails from IMAP (Gmail/Outlook), filters by FNOL keywords, ingests claims.
 * Uses Node.js (imapflow) - no Python required. Works on Vercel, Docker, etc.
 */
export async function POST() {
  try {
    const result = await syncInboxFromIMAP()

    return NextResponse.json({
      success: result.success,
      ingested: result.ingested,
      scanned: result.scanned,
      skippedNoFnol: result.skippedNoFnol,
      skippedDuplicate: result.skippedDuplicate ?? 0,
      errors: result.errors,
      hint: result.hint,
    })
  } catch (error) {
    console.error('Sync inbox error:', error)
    return NextResponse.json(
      { success: false, ingested: 0, scanned: 0, skippedNoFnol: 0, skippedDuplicate: 0, errors: [String(error)] },
      { status: 500 }
    )
  }
}
